import { Component, OnInit } from '@angular/core';
import { UtilProvider } from '../services/util-provider';
import { Router } from '@angular/router';
import { AppData } from '../services';

@Component({
  selector: 'app-add-money-wallet-confirm',
  templateUrl: './add-money-wallet-confirm.page.html',
  styleUrls: ['./add-money-wallet-confirm.page.scss'],
})
export class AddMoneyWalletConfirmPage implements OnInit {

  constructor(private util: UtilProvider, private router: Router,
    public appData: AppData
    ) { 
      this.appData.newTheme = this.util.getNewTheme();
    }

  ngOnInit() {
    this.util.showToast('Money added to wallet successfully', 'bottom');
    setTimeout(() => {
      this.router.navigate(['/wallet']);
    }, 2000);
  }
  

}
